// 
routers.beforeEach((to, from, next) => {
  let path = from.path
  if (to.path === '/load' || to.path === '/about' || to.path === '/register') {
    next()
  } else {
    let desc = store.state.user.desc
    if (!desc) {
      next('/load')
    } else {
      if (desc <= to.meta.requiresAuth) {
        next()
      } else {
        next(path)
      }
    }
  }
})
